<template>
  <v-container id="dashboard-view" fluid tag="section">
    <!--profile form-->
    <div>
      <form enctype="multipart/form-data" @submit.prevent="callModal()">
            <v-row justify="center">
              <v-col md="6">
                <v-stepper v-model="e1">
                  <v-stepper-header>
                    <v-stepper-step :complete="e1 > 1" step="1">
                     Provide Details
                    </v-stepper-step>

                    <v-divider />

                    <v-stepper-step :complete="e1 > 2" step="2">
                      Confirm Details
                    </v-stepper-step>
                  </v-stepper-header>

                  <v-stepper-items>
                    <v-stepper-content step="1">
                      <div>
                        <p class="display-1 font-weight-bold">
                          PROVIDE YOUR PERSONAL & COMPANY DETAILS
                        </p>
                        <p class="primary--text cursor" @click="more = true">
                          Why this is very important?
                        </p>
                        <v-dialog v-model="more" width="500">
                          <v-card>
                            <v-card-title class="headline">
                              More information about profile
                              <v-btn icon absolute right @click="more = false">
                                <v-icon>mdi-close</v-icon>
                              </v-btn>
                            </v-card-title>
                            <v-divider />
                            <v-card-text
                              class="black--text text-justify pt-2 text-32"
                            >
                             If you don't provide your complete profile and company details your campaign(s) will not be 
                             approved by Digit-Ad team
                            </v-card-text>
                          </v-card>
                        </v-dialog>
                      </div>
                      <v-divider class="my-5 py-1" />
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-select
                              :items="profile_fields"
                              label="SELECT FIELD TO UPDATE"
                              outlined
                              dense
                              v-model="field"
                            />
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-map
                                </v-icon>
                              </template>
                              <span>
                               Field To Update
                              </span>
                            </v-tooltip>
                            <v-icon color="green" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <v-divider class="my-5 py-1" />
                      <div>
                        <v-row>
                          <v-col md="10">
                            <v-text-field
                              dense
                              label=""
                              :placeholder="createPlaceHolder()"
                              outlined
                              v-model="inputData"
                            />
                          </v-col>
                          <v-col md="2" class="mt-2">
                            <v-tooltip right>
                              <template v-slot:activator="{ on, attrs }">
                                <v-icon
                                  v-bind="attrs"
                                  class="float-left"
                                  v-on="on"
                                >
                                  mdi-alert-circle-outline
                                </v-icon>
                              </template>
                              <span>
                                Provide data for the selected field
                              </span>
                            </v-tooltip>

                            <v-icon color="green" class="float-right">
                              mdi-check-bold
                            </v-icon>
                          </v-col>
                        </v-row>
                      </div>
                      <v-divider class="my-5 py-1" />

                      <v-btn
                        color="primary"
                        @click="e1 = 2"
                      >
                        Confirm
                      </v-btn>

                      <v-btn text>
                        Cancel
                      </v-btn>
                    </v-stepper-content>

                    <v-stepper-content step="2">
                      <v-card
                        class="mb-12"
                        color="grey lighten-1"
                       
                      >
                        <v-list two-line>
                          <v-list-item>
                            <v-list-item-icon>
                              <v-icon color="primary">
                                mdi-account
                              </v-icon>
                            </v-list-item-icon>

                            <v-list-item-content>
                              <v-list-item-title>
                                  {{inputData!==""?inputData:"No updates set"}}
                              </v-list-item-title>
                              <v-list-item-subtitle
                                >New {{field}}</v-list-item-subtitle
                              >
                            </v-list-item-content>
                          </v-list-item>
                        </v-list>
                      </v-card>

                      <v-btn color="primary" @click="e1 = 1">
                        Previous
                      </v-btn>

                      <v-btn text @click="e1 = 1">
                        Cancel
                      </v-btn>
                      <v-btn
                        color="primary"
                        class="float-right white--text"
                        type="submit"
                      >
                        Submit
                      </v-btn>
                    </v-stepper-content>
                  </v-stepper-items>
                </v-stepper>
              </v-col>
            </v-row>
          </form>
    </div>
    <v-dialog
      v-model="updateModal"
      transition="dialog-top-transition"
      max-width="400"
    >
      <v-card max-width="400">
        <v-toolbar
          color="primary"
          dark
        >
          <v-icon>mdi mdi-warning </v-icon> {{modalText.headerText}}
          <v-btn
            icon
            absolute
            right
            outlined
            class="float-right"
            color="white"
            @click="reload()"
          >
            <v-icon>mdi mdi-close</v-icon>
          </v-btn>
        </v-toolbar>
        <v-card-text>
          <p class="pt-4 text-center text-h3">
           {{modalText.questionText}} <br>
          </p>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn
            color="primary"
            @click="update_info()"
          >
            {{modalText.actionText}} <v-icon>mdi-check-outline</v-icon>
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import $ from "jquery";
import axios from "axios";
import Map from "./map";
import { mapGetters } from "vuex";

export default {
  name: "DashboardView",
  components: {
    Map,
  },
  methods: {
      callModal(){
          this.updateModal=true;
      },
      updateStore(){
          if(this.field=="Email"){
               this.userDetails.email=this.inputData;
           }else if(this.field=="Company Name"){
                this.userDetails.companyName=this.inputData;
           }else if(this.field=="Company Registration Number"){
                this.userDetails.company_reg_num=this.inputData;
           }else if(this.field=="Country"){
                this.userDetails.country=this.inputData;
           }else if(this.field=="Company Address"){
                this.userDetails.companyAddress=this.inputData;
           }else if(this.field=="Position In Company"){
                this.userDetails.position_in_company=this.inputData;
           }else if(this.field=="Home Address"){
                 this.userDetails.homeAddress=this.inputData;
           }else if(this.field=="City"){
                 this.userDetails.city=this.inputData;
           }
      },
      createPlaceHolder(){
           if(this.field=="Email"){
               this.placeholder="New Email Address";
               this.field_num=1;
           }else if(this.field=="Company Name"){
                this.placeholder="New Company Name";
                 this.field_num=4;
           }else if(this.field=="Company Registration Number"){
                this.placeholder="New Company Registration Number";
                 this.field_num=6;
           }else if(this.field=="Country"){
                this.placeholder="New Country";
                 this.field_num=2;
           }else if(this.field=="Company Address"){
                this.placeholder="New Company Address";
                 this.field_num=5;
           }else if(this.field=="Position In Company"){
                this.placeholder="New Position In Company";
                 this.field_num=7;
           }else if(this.field=="Home Address"){
                this.placeholder="New Home Address";
                 this.field_num=3;
           }else if(this.field=="City"){
                this.placeholder="New City";
                this.field_num=8;
           }else{
               this.placeholder="Select A Field To Update Above";
                this.field_num=0;
           }
           return this.placeholder;
      },
      reload(){
         this.$router.push("/components/profile/");
      },
    update_info() {
      let user_id = this.userDetails.user_id;
      let formData = new FormData();
      let details={field_num:this.field_num,update:this.inputData};
      let data_param = JSON.stringify(details);
      formData.append("data", data_param);
      formData.append("user_id", user_id);
      const params = { crossDomain: true, withCredentials: false }; //http://localhost/digit-ad/dashboard/campaign/submit_campaign.php
      const api = axios.create({ baseURL: "http://digitad.us-east-2.elasticbeanstalk.com" });
      api
        .post(
          "http://digitad.us-east-2.elasticbeanstalk.com/server/update_user_info.php",
          formData
        )
        .then((result) => {
             this.el=1;
          if (result.data == 1) {
            this.modalText.questionText="SUCCESSFUL!";
            this.modalText.actionText="Done!";
             this.updateStore();
          }else{
            this.modalText.questionText="FAILED!";
            this.modalText.actionText="ERROR!";
          }
        })
        .catch((error) => {
          console.log(error);
        });
      
    }
  },
  data: () => ({
    e1: 1,
   updateModal:false,
    placeholder:"",
    field:"",
    field_num:0,
    inputData:"",
    modalText:{
        headerText:"UPDATE YOUR PROFILE",
        actionText:"PROCEED",
        questionText:"ARE YOUR SURE OF THIS UPDATE?"
      },
    more: false,
    map: false,
    slot: "color:red",
    profile_fields: ["Email", "Country", "Company Name","Company Registration Number","Company Address","Position In Company","Home Address","City"],
    userInfo:{
        email:"",
        homeAddress:"",
        companyName:"",
        companyAddress:"",
        country:"",
        companyRegistrationNumber:"",
        position_in_company:"",
        city:""
    }
  }),

  computed: mapGetters([
    "userDetails"
  ]),
};
</script>
<style>
.card-elm:hover {
  border: 2px solid orange;
}
.map {
  /*background-image: url("https://www.nationsonline.org/maps/germany-administrative-map.jpg");*/
  /*background-color: #cccccc;*/
  background-size: 100%;
  width: 100%;
  min-height: 700px;
}
.v-dialog--fullscreen {
  margin-top: 3% !important;
  margin-left: 0;
  margin-right: 0;
  margin-bottom: 0;
  /* margin: 5.2%; */
}
.cursor {
  cursor: pointer;
}
.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}
.fade-enter, .fade-leave-to /* .fade-leave-active below version 2.1.8 */ {
  opacity: 0;
}
</style>
